import React, { Component } from 'react';
import glamorous from 'glamorous';
import PrettyInput from '../PrettyInput';
import PrettyLabel from '../PrettyLabel';
import tokens from '../../tokens';

const Row = glamorous.div({
  display: 'flex',
  alignItems: 'center',
});

const LabelContainer = glamorous.div({
  flexBasis: `${tokens.space*20}px`,
  textAlign: `right`,
  borderRight: `${tokens.gutter}px solid transparent`
});

const InputContainer = glamorous.div({

});


export default class extends Component {
  render() {
    return (
      <Row>
        <LabelContainer>
          <PrettyLabel>{this.props.label}:</PrettyLabel>
        </LabelContainer>
        <PrettyInput />
      </Row>
    );
  }
}



