const space = 8;
const tokens = {
  space: space,
  gutter: space * 2,
  font: {
    family: {
      primary: 'Work Sans',
      secondary: 'Slabo 27px0',
    },
    size: {
      tiny: `${space}px`,
      default: `${space*2}px`,
      regular: `${space*3}px`,
      title: `${space*4}px`,
      lead: `${space*5}px`,
      giant: `${space*6}px`,
    },
  }
}

export default tokens;



