import React, { Component } from 'react';
import glamorous from 'glamorous';
import tokens from '../../tokens';

const PrettyGutter = glamorous.div({
  padding: `${tokens.space*2}`,
});

export default class extends Component {
  render() {
    return (
      <PrettyGutter>
        { this.props.children }
      </PrettyGutter>
    )
  }
}



