import React, { Component } from 'react';
import glamorous from 'glamorous';
import PrettyFormRow from './components/PrettyFormRow';
import PrettyGutter from './components/PrettyGutter';
import tokens from './tokens';
import './fonts.css';
import './reset.css';

const AppContainer = glamorous.div({
  maxWidth: `${tokens.space*80}px`,
  display: 'block',
  border: '1px solid #EEE',
  padding: `${tokens.space*2}px`,
});

class App extends Component {
  render() {
    return (
      <AppContainer>
        <PrettyGutter>
          <PrettyFormRow label="task" />
        </PrettyGutter>
        <PrettyGutter>
          <PrettyFormRow label="description" />
        </PrettyGutter>
      </AppContainer>
    );
  }
}

export default App;



